import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import EmployeeInfo from '../components/empolyee/EmployeeDetails';
import Specification from '../components/empolyee/Specification';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { Employee } from '@/types/employee.types';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import EmployeeAttendance from '../components/employeeAttendance/EmployeeAttendance';
import AttendanceCardsStats from '../components/statistics/AttendanceCardsStats';
import UnderlineHeading from '@/components/heading/UnderlineHeading';

export default function EmployeeAttendancePage() {
    const params = useParams();
    const { id } = params;
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [employee, setEmployee] = useState<Employee | null>(null);

    useEffect(() => {
        const fetchEmployee = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.employee.show + id,
                });
                setEmployee(response.data.data);
            } catch (error) {
                setError('Failed to load employee details');
            } finally {
                setLoading(false);
            }
        };
        if (id) {
            fetchEmployee();
        } else {
            setError('Invalid employee ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!employee) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Employee not found</h2>
            </div>
        );
    }
    return (
        <div className="w-full mt-6 mb-24">
            <UnderlineHeading text="بيانات الحضور" />
            <AttendanceCardsStats />
            <EmployeeAttendance />
        </div>
    );
}
